* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 20px;
  background-color: #edf2f6;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.formulario {
  display: flex;
  flex-direction: column;
  width: 300px;
  align-items: center;
  margin: 0 auto;
}

.formulario .input-group {
  margin: 15px;
  display: flex;
  align-items: center;
  height: 20px;
}

.formulario input {
  padding: 5px;
}

.formulario button {
  padding: 5px;
}

.delete {
  font-size: 13px;
  border-radius: 10px;
  color: white;
  padding: 8px;
  cursor: pointer;
  background-color: rgb(240, 48, 34);
  margin-left: 10px;
}

.tweet-container {
  background-color: white;
  border-radius: 5px;
  max-width: 500px;
  margin: 15px auto;
  box-shadow: 0 0.2px 0.9px -3px rgba(0, 0, 0, 0.022),
    0 0.5px 2.3px -3px rgba(0, 0, 0, 0.031),
    0 1.1px 4.6px -3px rgba(0, 0, 0, 0.039),
    0 2.2px 9.5px -3px rgba(0, 0, 0, 0.048), 0 6px 26px -3px rgba(0, 0, 0, 0.07);
}
.tweet {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  /* background-color: lightblue; */
}

.tweet-info {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.tweet-autor {
  font-size: 12px;
}

.acciones {
  display: flex;
  align-items: center;
}

.likes {
  cursor: pointer;
  font-size: 13px;
  border-radius: 10px;
  color: black;
  padding: 8px;
  cursor: pointer;
  border: 1px solid rgb(240, 48, 34);
  margin-left: 10px;
  display: flex;
  align-items: center;
}

.likes img {
  margin-right: 5px;
}
